<template>
  <div>
    <div class="reservation-top">
      <div class="reservation-top-filter">
        <div class="filter-name filter-item">
          <span>:</span>
          <el-input v-model="username" class="filter-input" placeholder=""></el-input>
        </div>
        <div class="filter-tel filter-item">
          <span>:</span>
          <el-input v-model="telNum" maxlength=11 class="filter-input" placeholder=""></el-input>
        </div>
        <div class="filter-gender filter-item">
          <span></span>
          <el-select class="filter-input" v-model="selectGender" placeholder="">
            <el-option
              v-for="item in gender"
              :key="item.value"
              :label="item.label"
              :value="item.label"
            ></el-option>
          </el-select>
        </div>
        <div class="time-block filter-item">
          <span>:</span>
          <el-date-picker
            class="time-block-select"
            v-model="registrationDate"
            type="datetimerange"
            range-separator=""
            value-format="yyyy-MM-dd HH:mm:ss"
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
        </div>
        <div class="time-block filter-item">
          <span>:</span>
          <el-date-picker
            class="time-block-select"
            v-model="reservationDate"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetimerange"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
        </div>
        <div class="reservation-button">
          <el-button @click="search" type="primary"></el-button>
        </div>
        <div class="reservation-button">
          <el-button @click="clean" type="primary"></el-button>
        </div>
        <div class="reservation-button">
          <el-button @click="exportExcel" type="primary"></el-button>
        </div>
      </div>
    </div>
    <div>
      <el-table
        v-loading="listLoading"
        :data="tableData"
        border
        style="width: 100%"
      >
        <el-table-column
          prop="reservationTime"
          label=""
          width="180"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="150"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="phone"
          label=""
          width="170"
          align="center"
        ></el-table-column>
<!--        <el-table-column-->
<!--          prop="type"-->
<!--          label=""-->
<!--          width="100"-->
<!--          align="center"-->
<!--        ></el-table-column>-->
        <el-table-column
          prop="sex"
          label=""
          width="70"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="age"
          label=""
          width="70"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="job"
          label=""
          width="200"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="question"
          label=""
          align="center"
        ></el-table-column>
        <el-table-column
          prop="createdTime"
          label=""
          width="180"
          align="center"
        ></el-table-column>
      </el-table>
    </div>
    <div class="pagination-block">
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page.sync="pagination.currentPage"
        :page-size="pagination.row"
        layout="prev, pager, next, jumper"
        :total="pagination.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
    import API from '@/api/usr';
    import waves from '@/directive/waves';
    import btf from 'btf';
    export default {
        name: "",
        directives: {
            waves
        },
        data() {
            return {
                listLoading:true,
                username: '',
                selectGender: '',
                telNum: '',
                gender: [{
                    value: 1,
                    label: ''
                }, {
                    value: 2,
                    label: ''
                }],
                registrationDate: [],
                reservationDate: [],
                tableData: [],
                pagination: {
                    currentPage: 1,
                    row: 10,
                    total: 0
                }
            }
        },
        methods: {
            search() {
                this.tableData = [];
                this.pagination.currentPage = 1;
                this.init()
            },
            clean() {
                this.username = '';
                this.selectGender = '';
                this.registrationDate = [];
                this.reservationDate = [];
                this.telNum = '';
                this.tableData = [];
                this.pagination.total = 0;
                this.init();
            },
            exportExcel(){
              let that = this;
              async function f () {
                const loading = that.$loading({
                  lock: true,
                  text: 'Loading',
                  spinner: 'el-icon-loading',
                  background: 'rgba(0, 0, 0, 0.7)'
                });
               let data = await API.reservationInquiry({
                 page:that.pagination.currentPage,
                 rows:999999,
                 name:that.username,
                 sex:that.selectGender===''?1:that.selectGender===''?2:'',
                 createdTimeStart:that.registrationDate[0],
                 createdTimeEnd:that.registrationDate[1],
                 reservationTimeStart:that.reservationDate[0],
                 reservationTimeEnd:that.reservationDate[1],
                 phone:that.telNum,
               }).then(res=>{
                 if (res.pageCount<=0)
                   that.$message({
                     message:''
                   });
                 let temp = [...res.data];
                 temp.forEach((i)=>{
                   if(i.sex==="1"){
                     i.sex=""
                   }else {
                     i.sex = ''
                   };
                   if (i.type===1){
                     i.type=""
                   }else{
                     i.type=""
                   }
                 })
                 return [...temp]
               })
                let tempdata = [];
                for (let i of data){
                  tempdata.push({
                    '':i.reservationTime,
                    '':i.name,
                    '':i.phone,
                    '':i.sex,
                    '':i.age,
                    '':i.job,
                    '':i.question,
                    '':i.createdTime,
                  })
                };
                loading.close();
                btf({ data:[...tempdata] });
              }
              f()
            },
            handleCurrentChange(v) {
                this.pagination.currentPage = v;
                this.init();
            },
            init() {
                let obj = arguments;
                this.listLoading = true;
                API.reservationInquiry({
                    page:this.pagination.currentPage,
                    rows:this.pagination.row,
                    name:this.username,
                    sex:this.selectGender===''?1:this.selectGender===''?2:'',
                    createdTimeStart:this.registrationDate[0],
                    createdTimeEnd:this.registrationDate[1],
                    reservationTimeStart:this.reservationDate[0],
                    reservationTimeEnd:this.reservationDate[1],
                    phone:this.telNum,
                }).then(res=>{
                    if (res.pageCount<=0)
                    this.$message({
                        message:''
                    });
                    this.listLoading = false;
                    let temp = [...res.data];
                    temp.forEach((i)=>{
                        if(i.sex==="1"){
                            i.sex=""
                        }else {
                            i.sex = ''
                        };
                        if (i.type===1){
                            i.type=""
                        }else{
                            i.type=""
                        }
                    })
                    this.tableData = [...temp];
                    this.pagination.total = res.totalRecords;
                    return [...temp]
                }).catch(e=>{
                    this.listLoading = false;
                    // this.$message.error('')
                })
            }
        },
        mounted() {
            this.init()
        }
    }
</script>

<style scoped>
  .reservation-top-filter {
    width: 100%;
  }

  .filter-item {
    float: left;
    margin-bottom: 20px;
    margin-left: 20px;
    width: 300px;
  }

  .filter-item span {
    width: 17px;
  }

  .time-block {
    width: 550px;
  }

  .time-block span {
    width: 17px;
  }

  .time-block-select {
    margin-left: 20px;
  }

  .filter-item .filter-input {
    margin-left: 20px;
    display: inline-block;
    width: 200px;
  }

  .reservation-button {
    width: 100px;
    display: inline-block;
    margin-bottom: 20px;
    float: left;
  }

  .pagination-block {
    margin-top: 20px;
    text-align: right;
  }
</style>
