const XLSX = require('xlsx');
const saveAS = require('file-saver');

function s2ab(s) {
    const buf = new ArrayBuffer(s.length);
    const view = new Uint8Array(buf);
    for (var i=0; i!=s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
    return buf;
}

//A-Z;
function getCharCol(n) {
    if (n === 0) {
        return 'A'
    }
    let temCol = '',
        s = '',
        m = 0
    while (n > 0) {
        m = n % 26 + 1
        s = String.fromCharCode(m + 64) + s
        n = (n - m) / 26
    }
    return s
}

export default function btf({data, filename = 'NewExcel', autoWidth = true} = {}) {
    let _header = Object.keys(data[0]);
    let header = Object.keys(data[0]).map((item, index) => {
        return {v: item, position: String.fromCharCode(65 + index) + 1}
    });

    let sheetHeader = {};
    for (let i of header) {
        sheetHeader[i.position] = {v: i.v};
    }
    //XLSX workbook

    let _data = [];
    data.map((item, index) => {
        _data[index] = [];
        for (let i of _header) {
            _data[index].push(item[i])
        }
    });
    let sheetData = {};
    let tempData = data.map((v, i) => _header.map((k, j) => Object.assign({}, {
        v: v[k],
        position: String.fromCharCode(65 + j) + (i + 2)
    }))).reduce((prev, next) => prev.concat(next));
    for (let i of tempData){
        sheetData[i.position]={v:i.v}
    }
    //XLSX workbook

    const outData = {...sheetHeader,...sheetData};
    const allPosition = Object.keys(outData);

    const sheetRange = allPosition[0]+':'+allPosition[allPosition.length-1];

    const workBook = {
        SheetNames:['Sheet1'],
        Sheets:{
            'Sheet1':{...outData,...{'!ref':sheetRange}}
        }
    };

    const writingOptions = {bookType:'xlsx',bookSST:false,type:'binary'};
    const workBookOut = XLSX.write(workBook,writingOptions);

    saveAS(new Blob([s2ab(workBookOut)],{type:""}),`${filename}.xlsx`)
}
